.responseMsgContainer,
.responseMsgErrorContainer {
  display: flex;
  position: fixed;
  bottom: 20px;
  left: 20px;
  padding: 10px;
  color: white;
  border-radius: 2px;
  box-shadow: 0.5px 0.5px 2px 1px #202020;
  z-index: 2;
  opacity: 1;
  transition: opacity 0.7s ease, z-index 0.7s ease;
}
.responseMsgContainerFadeOut {
  z-index: -1;
  opacity: 0;
}
.responseMsgContainer {
  background-color: #19a819;
}
.responseMsgErrorContainer {
  background-color: #df2a2a;
}
.closeResponseModal {
  margin-left: 8px;
  cursor: pointer;
  background-color: transparent;
  color: #0e0e0e;
  border: none;
}
.main {
  position: relative;
}
.bottomPart {
  background-image: linear-gradient(rgba(44, 33, 45, 0.17), 90%, rgba(44, 33, 45, 0.51));
}
.sectionLinks {
  display: flex;
  width: 62%;
  box-shadow: 0px 0px 14px -6px #999 inset;
  border-radius: 0 0 40px 0;
  font-size: var(--global-font-small);
  border-bottom: 1px #3d4e4f44 solid;
}

.sectionLinks > * {
  line-height: 60px;
  cursor: pointer;
  flex-grow: 1;
  text-align: center;
  border-radius: 0 0 40px 0;
  font-size: 1.2em;
  transition: all 0.2s ease;
}
.sectionLinks > *:hover {
  font-size: 1.25em;
  color: rgb(59, 20, 75);
  text-shadow: 0 0 0.5px rgb(59, 20, 75);
  box-shadow: -7px 0px 14px -12px #999 inset;
}
.clickedSectionLink {
  border-right: 2px solid rgb(48, 54, 62);
  box-shadow: -12px -4px 14px -17px #ffffff inset;
}
.sectionsContainer {
  display: flex;
  padding: 22vh 0;
}
.sectionsContainerMobile {
  display: flex;
  padding: 22vh 0;
  overscroll-behavior-x: contain;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.sectionsContainerMobile > * {
  scroll-snap-align: start;
}
.main > :first-child > * {
  /* previousArrowContainer nextArrowContainer */
  position: absolute;
  height: 100%;
  z-index: 2;
}
.main > :first-child > :nth-child(even) {
  /* nextArrowContainer*/
  right: 0;
}
.main > :first-child > * > * {
  /* nextArrow previousArrow*/
  position: sticky;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50%;
  cursor: pointer;
  transform: scale(1);
  transition: transform 200ms ease;
}
.main > :first-child > :nth-child(odd) > * {
  /* previousArrow*/
  transform: rotate(180deg);
}

@media (max-width: 650px) {
  .bottomPart {
    padding-top: 23vh;
  }
  .sectionsContainer {
    padding: 13vh 0 7vh 0;
  }
  .sectionLinks {
    position: relative;
    border-bottom-left-radius: 40px;
    box-shadow: 0px 11px 22px #18363924;
    border: 2px solid rgb(17, 51, 62);
    margin: auto;
    background: linear-gradient(145deg, rgb(216, 225, 226), rgb(141, 150, 151));
    flex-direction: column;
    padding: 0 11%;
  }
  .sectionLinks::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 0 0 40px 40px;
    /* box-shadow: -20px -7px 25px #0000003a inset; */
  }
  .sectionLinks > * {
    z-index: 5;
  }
  /*   .main > :first-child > * {
    display: none;
  } */
}
